ul.sf-menu.sf-accordion,
ul.sf-menu.sf-accordion .sf-hidden{
  display: none !important;
  position: absolute;
}
ul.sf-menu.sf-accordion{
  &,
  ul,
  li {
    float: left;
    width: 100%;
  }
  ul {
    margin: 0;
    padding: 0;
  }

  &.sf-expanded,
  li.sf-expanded > ul {
    left: auto !important;
    position: relative;
    top: auto !important;
    display: block !important;
  }
 li {
   &.menuparent {
     > a {
       @include font-icon('\f107', 24px, 8px, 6px, 29px, white, white, true, false);
       &:after {
         transition: all .4s cubic-bezier(.55,.085,0,.99);
       }
     }
     &.sf-expanded {
       > a:after {
         transform: rotate(180deg);
       }
     }
   }
 }
}

.sf-menu,
.sf-menu * {
  list-style: none;
  margin: 0;
  padding: 0;
}
.sf-menu {
  line-height: 1.25;
  z-index: 497;


  a,
  span.nolink {
    display: block;
    position: relative;
    &:hover {
      border-bottom: none;
    }
  }
  &.sf-horizontal {
    ul {
      left: 0;
      position: absolute;
      top: -99999em;
      width: 12em;
      li {
        width: 100%;
      }
    }
    li {
      float: left;
      position: relative;
      z-index: 498;
      &:hover > ul,
      &.sfHover > ul {
        left: 0;
        top: 2.5em;
      }
      li:hover > ul,
      li.sfHover > ul {
        left: 12em;
        top: 0;
      }
    }
    .sf-hidden {
      left: -99999em !important;
      top: -99999em !important;
    }
  }
}


.sf-menu.menu,
.sf-menu.menu ul,
.sf-menu.menu li {
  margin: 0;
}

.sf-menu a.sf-with-ul,
.sf-menu span.nolink.sf-with-ul {
  min-width: 1px;
}
.sf-sub-indicator {
  background: url('../images/arrows-ffffff.png') no-repeat -10px -100px;
  display: block;
  height: 10px;
  overflow: hidden;
  position: absolute;
  right: 0.75em;
  text-indent: -999em;
  top: 1.05em;
  width: 10px;
}
a > .sf-sub-indicator,
span.nolink > .sf-sub-indicator {
  top: 0.8em;
  background-position: 0 -100px;
}
a:focus > .sf-sub-indicator,
a:hover > .sf-sub-indicator,
a:active > .sf-sub-indicator,
li:hover > a > .sf-sub-indicator,
li.sfHover > a > .sf-sub-indicator,
span.nolink:hover > .sf-sub-indicator,
li:hover > span.nolink > .sf-sub-indicator,
li.sfHover > span.nolink > .sf-sub-indicator {
  background-position: -10px -100px;
}
.sf-menu ul .sf-sub-indicator {
  background-position: -10px 0;
}
.sf-menu ul a > .sf-sub-indicator,
.sf-menu ul span.nolink > .sf-sub-indicator {
  background-position: 0 0;
}
.sf-menu ul a:focus > .sf-sub-indicator,
.sf-menu ul a:hover > .sf-sub-indicator,
.sf-menu ul a:active > .sf-sub-indicator,
.sf-menu ul li:hover > a > .sf-sub-indicator,
.sf-menu ul li.sfHover > a > .sf-sub-indicator,
.sf-menu ul span.nolink:hover > .sf-sub-indicator,
.sf-menu ul li:hover > span.nolink > .sf-sub-indicator,
.sf-menu ul li.sfHover > span.nolink > .sf-sub-indicator {
  background-position: -10px 0;
}
.sf-menu.sf-horizontal.sf-shadow ul,
.sf-menu.sf-vertical.sf-shadow ul,
.sf-menu.sf-navbar.sf-shadow ul ul {
  background: url('../images/shadow.png') no-repeat right bottom;
  padding: 0 8px 9px 0 !important;
  -webkit-border-top-right-radius: 8px;
  -webkit-border-bottom-left-radius: 8px;
  -moz-border-radius-topright: 8px;
  -moz-border-radius-bottomleft: 8px;
  border-top-right-radius: 8px;
  border-bottom-left-radius: 8px;
}
.sf-shadow ul.sf-shadow-off {
  background: transparent;
}
.sf-menu.rtl,
.sf-menu.rtl li {
